.m .consent-document-viewer {
  display: flex;
  flex-direction: column;
  margin-top: 9px;
  margin-bottom: 72px;
}
.m .consent-document-viewer .consent-header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-bottom: 8px;
}
.m .consent-document-viewer .consent-header .consent-header-left {
  display: flex;
  justify-content: flex-start;
}
.m .consent-document-viewer .consent-header .consent-header-right {
  display: flex;
  justify-content: flex-end;
}
.m .consent-document-viewer .consent-body {
  display: flex;
  width: 100%;
  height: 100%;
  margin-top: 8px;
  overflow: hidden;
}
.m .consent-document-viewer .consent-footer {
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}
.m .consent-document-viewer .consent-footer .consent-instructions {
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
}
.m .consent-document-viewer .consent-footer .consent-instructions .top-text {
  margin-bottom: 4px;
}
.m .consent-document-viewer .consent-footer .consent-instructions .bottom-text {
  margin-top: 4px;
}
.m .consent-document-viewer .consent-footer .doc-changer {
  display: flex;
  margin-top: 12px;
}
.m .consent-document-viewer .consent-footer .doc-changer .nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.m .consent-document-viewer .consent-footer .doc-changer .nav-container .icons {
  width: 32px;
  font-size: 32px;
  height: 32px;
}
.m .consent-document-viewer .consent-footer .doc-changer .nav-container .icons.disabled {
  color: lightgray;
}
.m .consent-document-viewer .consent-footer .doc-changer .image-navigation-wrapper {
  height: auto;
  width: 100%;
}
.m .consent-document-viewer .consent-footer .doc-changer .preview-block {
  width: 80px;
  height: auto;
  margin-right: 8px;
}
.m .consent-document-viewer .consent-footer .doc-changer .image-navigation {
  width: auto;
  overflow: auto;
  display: flex;
  flex-direction: row;
}
.m .consent-document-viewer .consent-footer .doc-changer .case-image {
  height: 80px;
  width: 80px;
  border-radius: 8px;
}
.m .consent-document-viewer .consent-footer .doc-changer .case-image.outline {
  border: 2px solid black;
}
.m .consent-document-viewer .consent-footer .doc-changer .adverse-event-image-modal .ant-modal-body {
  padding-bottom: 60px;
  min-height: 100%;
}
.m .consent-document-viewer .consent-footer .doc-changer .adverse-event-image-modal .ant-modal-footer {
  padding: 0;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 1360px;
  background: white;
  float: right;
  z-index: 2;
}
.m .ant-btn.prev[disabled],
.m .ant-btn.next[disabled] {
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border: transparent;
}
@media (min-width: 1025px) {
  .m .consent-document-viewer {
    margin-top: 170px;
  }
}
.m .pdf-viewer {
  padding-bottom: 60px;
  width: 100%;
}
.m .pdf-viewer .react-pdf__Page__canvas {
  margin: auto;
}
.m .pdf-viewer .page-number {
  height: 60px;
  margin: 10px 10px 10px 0px;
  align-items: center;
  justify-content: flex-end;
  bottom: 72px;
  display: flex;
  flex-direction: row;
}
.m .pdf-viewer .page-number .text {
  padding: 10px 10px 0px 10px;
}
.m .pdf-viewer .padding-10 {
  padding: 10px;
}
