.m .fixed_header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 64px;
  z-index: 100;
}
.m .welcomeMessage {
  display: flex;
  position: absolute;
  top: 0px;
  justify-content: flex-end;
  align-items: center;
  height: 90px;
  width: 100%;
  right: 20px;
}
.m .welcomeMessage > h3 {
  color: white;
  font-family: AvenirNext-Medium;
}
.m .user-logo-container {
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 12px;
}
.m .user-logo {
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 12px;
}
.m .mainmenu {
  margin-left: 50px;
  margin-top: 16px;
}
.m .ico-search-desktop {
  width: 24px;
  height: 24px;
  margin-right: 24px;
  object-fit: contain;
}
.m .chat {
  width: 24px;
  height: 24px;
  margin-right: 24px;
  object-fit: contain;
}
.m .alert {
  width: 24px;
  height: 24px;
  margin-right: 24px;
  object-fit: contain;
}
.m .add-button-menu {
  margin-top: 16px;
}
.m .ico-search-desktop:hover {
  cursor: pointer;
}
.m .chat:hover {
  cursor: pointer;
}
.m .alert:hover {
  cursor: pointer;
}
.m .searchbar {
  display: block;
  position: fixed;
  top: 60px;
  left: 0;
  right: 0;
  height: 48px;
  z-index: 100;
}
.m .addButton.ant-btn {
  margin-right: 24px;
  background: #005587;
  width: 92px;
  height: 40px;
  border-radius: 20px;
  color: #ffffff;
  border: 0;
}
.m .icons {
  width: 18px;
  height: 18px;
}
.m .nosearchbar {
  display: none;
}
.m .addButtonMenuItem.ant-dropdown-menu-item:hover {
  background-color: #d4d7d9;
}
